import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { StockChartComponent } from 'src/app/shared/widgets/stock-chart/stock-chart.component';

@Component({
  selector: 'app-chart',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.scss']
})
export class ChartComponent implements OnInit {
  @ViewChild('inputSymbol') inputSymbol: ElementRef;

  @ViewChild(StockChartComponent) stockChart: StockChartComponent;


  constructor() { }

  ngOnInit(): void {
  }

  updateStockChart(event: any) {
    let symbol = event.target.value.toUpperCase();
    
    this.stockChart.createStockChart(symbol); 

    this.inputSymbol.nativeElement.value = '';
  }

}
